$main: #081d47;
$accent: #db3120;
$background: #fff;
$secondary-background: #edf6fe;
$dark-color: #eeeeee;
$secondary-text: #000;

 :root {
    --main-color: #{$main};
    --accent-color: #{$accent};
    --dark-color: #{$dark-color};
    --background-color: #{$background};
    --secondary-background-color: #{$secondary-background};
    --secondary-text-color: #{$secondary-text}; }
