.img {
    display: block;

    &-wrapper {

        &--sm {
            height: 250px; }

        &--md {
            height: 450px; }

        &--lg {
            height: 550px; } }

    &--flexible {
        object-fit: cover;
        width: 100%;
        height: 100%; }

    &--contain {
        object-fit: contain;
        width: 100%;
        height: 100%; } }
