.footer {
    padding: 50px 0;
    background-color: var(--dark-color);
    color: var(--secondary-text-color);

    &__content {
        display: flex;
        flex-direction: column; }

    &__item {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        &:not(:last-child) {
            margin-bottom: 50px; } }

    &__logo {
        @media ( max-width: 678px ) {
            flex-basis: 100%;
            text-align: center;
            margin-bottom: 20px; } }

    &__logo-name {
        display: block;
        font-size: 22px;
        font-weight: bold;
        margin-bottom: 10px; }

    &__logo-desc {
        display: block;
        font-size: 14px;
        text-transform: uppercase; }

    &__brand {
        border-top: 1px solid #e9e9e9;
        padding-top: 20px;

        &-text {
            width: 100%;
            font-size: 14px;
            line-height: 20px;
            text-align: center; } }

    &__title {
        font-size: 14px;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 25px; }

    &__nav-menu {

        @media ( max-width: 678px ) {
            flex-basis: 100%;
            margin-bottom: 20px; }

        &-link {
            color: var(--accent-color);
            text-transform: uppercase;
            text-decoration: none;
            font-size: 14px;

            &:hover {
                text-decoration: underline; } } } }
