@import url(vendor/normalize.css);
@import url(vendor/simple-grid.css);
:root {
  --main-color: #081d47;
  --accent-color: #db3120;
  --dark-color: #eeeeee;
  --background-color: #fff;
  --secondary-background-color: #edf6fe;
  --secondary-text-color: #000;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Thin.woff2") format("woff2");
  font-weight: 100;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  font-family: "Roboto";
}

body {
  color: var(--main-color);
  background-color: var(--background-color);
}

.site-wrapper {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}

#content {
  margin: 30px 0;
}

.container {
  max-width: 1366px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.container-fluid {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--background-color);
  border-bottom: 1px solid #e9e9e9;
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 120px;
  padding: 20px 0;
}

.header__nav-menu {
  height: 100%;
}

.header__nav-menu-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (max-width: 992px) {
  .header__nav-menu-list {
    display: none;
  }
}

.header__nav-menu-btn {
  display: none;
}

@media (max-width: 992px) {
  .header__nav-menu-btn {
    display: inline-block;
  }
}

.header__nav-menu-item:not(last-child) {
  margin-right: 20px;
}

.header__nav-menu-link {
  color: var(--main-color);
  text-decoration: none;
}

.header__nav-menu-link:hover {
  color: var(--accent-color);
}

.logo {
  display: flex;
  text-decoration: none;
  color: inherit;
}

.logo__img-wrapper {
  width: 70px;
  height: 70px;
  margin-right: 20px;
}

.logo__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.logo__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 600px) {
  .logo__text {
    display: none;
  }
}

.logo__name {
  margin: 0;
  font-size: 22px;
}

.logo__desc {
  font-size: 11px;
  text-transform: uppercase;
}

.main {
  padding-top: 121px;
  flex: 1 1 auto;
}

.footer {
  padding: 50px 0;
  background-color: var(--dark-color);
  color: var(--secondary-text-color);
}

.footer__content {
  display: flex;
  flex-direction: column;
}

.footer__item {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer__item:not(:last-child) {
  margin-bottom: 50px;
}

@media (max-width: 678px) {
  .footer__logo {
    flex-basis: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
}

.footer__logo-name {
  display: block;
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
}

.footer__logo-desc {
  display: block;
  font-size: 14px;
  text-transform: uppercase;
}

.footer__brand {
  border-top: 1px solid #e9e9e9;
  padding-top: 20px;
}

.footer__brand-text {
  width: 100%;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
}

.footer__title {
  font-size: 14px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 25px;
}

@media (max-width: 678px) {
  .footer__nav-menu {
    flex-basis: 100%;
    margin-bottom: 20px;
  }
}

.footer__nav-menu-link {
  color: var(--accent-color);
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
}

.footer__nav-menu-link:hover {
  text-decoration: underline;
}

.section {
  padding: 50px 0 60px 0;
}

.section__title {
  font-size: 30px;
  margin-top: 0;
  margin-bottom: 40px;
  line-height: 1.2;
  letter-spacing: .05em;
}

.section__title--center {
  text-align: center;
}

.section__text {
  display: block;
  font-size: 14px;
  color: var(--accent-color);
  text-transform: uppercase;
  margin-bottom: 10px;
}

.section__text--center {
  text-align: center;
}

.link {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s, background 0.2s;
}

.link--details {
  display: inline-block;
  border-bottom: 2px solid var(--accent-color);
  padding-bottom: 5px;
  font-weight: bold;
}

.link--details:hover {
  color: var(--accent-color);
}

button.btn {
  background: transparent;
}

a.btn {
  display: inline-block;
  text-decoration: none;
  min-width: 200px;
  padding: 15px 10px;
  background: var(--accent-color);
  color: white;
}

.list li {
  margin-bottom: 10px;
}

.list--reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.img {
  display: block;
}

.img-wrapper--sm {
  height: 250px;
}

.img-wrapper--md {
  height: 450px;
}

.img-wrapper--lg {
  height: 550px;
}

.img--flexible {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.img--contain {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.vh {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.hero {
  display: flex;
  align-items: center;
  height: calc(100vh - 121px);
  min-height: 600px;
}

.hero__content {
  display: flex;
  align-items: center;
  padding: 40px;
  border-radius: 5px;
  background-color: var(--secondary-background-color);
}

@media (max-width: 600px) {
  .hero__content {
    flex-direction: column-reverse;
    background-color: transparent;
    padding: 0;
    text-align: center;
  }
}

.hero__item {
  flex-basis: 50%;
}

.hero__img {
  height: 450px;
}

@media (max-width: 992px) {
  .hero__img {
    height: 300px;
  }
}

@media (max-width: 768px) {
  .hero__img {
    height: 250px;
  }
}

@media (max-width: 600px) {
  .hero__img {
    width: 100%;
    margin-bottom: 30px;
  }
}

.hero__title {
  margin: 0;
  font-size: 37px;
}

.hero__desc {
  font-size: 18px;
  margin: 20px 0;
}

.about__item {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.about__title {
  font-size: 25px;
  margin-top: 0;
  margin-bottom: 20px;
}

.about__text {
  display: block;
  flex: 1 1 auto;
  margin-top: 0;
  margin-bottom: 20px;
}

.news,
.projects {
  background-color: var(--secondary-background-color);
}

.news__link {
  display: block;
}

.news__link:hover .news__title {
  color: var(--accent-color);
}

.news__date {
  display: inline-block;
  margin: 15px 0;
}

.news__title {
  transition: color 0.2s;
  margin: 0;
}

.projects__title {
  transition: color 0.2s;
  margin: 15px 0 0 0;
}

.projects__link {
  display: block;
}

.projects__link:hover .projects__title {
  color: var(--accent-color);
}

.alert__content {
  display: flex;
  align-items: center;
}

@media (max-width: 992px) {
  .alert__content {
    flex-direction: column;
  }
}

.alert__item {
  flex-basis: 50%;
}

.alert__item:not(:last-child) {
  margin-bottom: 30px;
}

.alert__heading {
  text-align: center;
}
