*,
*:before,
*:after {
    box-sizing: border-box; }

html, body {
    height: 100%;
    font-family: "Roboto"; }

body {
    color: var(--main-color);
    background-color: var(--background-color); }

.site-wrapper {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column; }

#content {
    margin: 30px 0; }
